.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

button {
  padding: 20px;
  font-weight: bold;
  font-size: small;
  width: 50%;
  position: relative;
  margin-bottom:20px;
  left: 25%;
}

input[type="text"] {
  font-size: medium;
  width: 50%;
  position: relative;
  left: 25%;
  margin-bottom:20px;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}

h1 { color: #111; font-family: 'Helvetica Neue', sans-serif; font-size: 100px; font-weight: bold; letter-spacing: -1px; line-height: 1; text-align: center; }


h2 { color: #111; font-family: 'Helvetica Neue', sans-serif; font-size: 30px; font-weight: bold; line-height: 32px; margin: 0 0 15px; text-align: center; }

h3 { color: #111; font-family: 'Open Sans', sans-serif; font-size: 20px; font-weight: 500; line-height: 32px; margin: 0 0 0px; }


p { color: #685206; font-family: 'Helvetica Neue', sans-serif; font-size: 14px; line-height: 24px; margin: 0 0 24px; text-align: justify; text-justify: inter-word; }
.segments { color: #685206; font-family: 'Helvetica Neue', sans-serif; font-size: 14px; margin: 0 0 5px; }

/* p {
  font-size: medium;
} */

div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
  padding: 10px;
}

.talk-bubble-info {
  margin: 0px;
}

.talk-bubble {
  margin-left: 40px;
  margin-top: 0px;
  margin-bottom: 0px;
  display: inline-block;
  position: relative;
  width: 70%;
  height: auto;
  background-color: lightyellow;
}

.talk-bubble:after{
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: -20px;
  right: auto;
  top: 0px;
  bottom: auto;
  border: 22px solid;
  border-color: lightyellow transparent transparent transparent;
}

ul {
  list-style-type: none;
}

.bubble {
  background-color:#efefef
}

#feedback-form {
  height: 100vh
}


